import { createApp } from 'vue'
import App from '@/App.vue'
import router from './router'
import axios from 'redaxios'
import { PostIndex } from './types/PostIndex'
import Axios from 'axios'

const loadApp = async () => {
  await Axios({ url: 'http://39.102.136.57:8088/blogs/blogcontents/' }).then(
    res => {
      console.log(res.data)
    }
  ).catch(res => {
    console.log(res)
  }
  )
  const dataPath = 'blog_store/posts_index.json'
  const { data: postsIndex } = await axios.get<PostIndex[]>(dataPath)
  const blogSections: Record<string, number> = postsIndex.reduce(
    (prev, { section }) =>
      prev[section]
        ? { ...prev, [section]: prev[section] + 1 }
        : { ...prev, [section]: 1 },
    { all: postsIndex.length } as Record<string, number>
  )

  createApp(App)
    .use(router)
    .provide<PostIndex[]>('postsIndex', postsIndex)
    .provide<Record<string, number>>('blogSections', blogSections)
    .mount('#app')
}

loadApp()
